// =============================================================
// 1. IMPLEMENTASI API (DIPINDAHKAN DARI lib/api.ts)
// =============================================================
const JIKAN_API_BASE = 'https://api.jikan.moe/v4'

export interface Anime {
  mal_id: number
  title: string
  title_english?: string
  images: {
    jpg: {
      image_url: string
      small_image_url: string
      large_image_url: string
    }
  }
  type?: string
  synopsis?: string
  episodes?: number | null
  status?: string
  score?: number
  genres?: Array<{ mal_id: number; name: string }>
  studios?: Array<{ mal_id: number; name: string }>
  year?: number | null
  aired?: {
    from: string
    to?: string | null
  }
  source?: string 
  trailer?: { 
    url?: string | null
    embed_url?: string | null
    youtube_id?: string | null
  } | null
  relations?: Array<{ 
    relation: string
    entry: Array<{
      mal_id: number
      type: string
      name: string
      url: string
    }>
  }>
}

// Fungsi penunda untuk menghindari rate limiting API
async function rateLimit() {
  return new Promise((resolve) => setTimeout(resolve, 300))
}

export async function getAnimeDetail(mal_id: number): Promise<Anime | null> {
  try {
    await rateLimit() 
    
    // Menggunakan cache: 'no-store' untuk memastikan selalu mengambil data terbaru dari Jikan
    const res = await fetch(`${JIKAN_API_BASE}/anime/${mal_id}/full`, { cache: 'no-store' }) 
    
    // Jika API merespons 404 atau status non-OK lainnya
    if (!res.ok) {
        // Log status untuk debugging di konsol
        console.error(`API returned status ${res.status} for ID ${mal_id}.`);
        if (res.status === 404) return null; // Anime tidak ditemukan
        throw new Error('Failed to fetch anime detail with status: ' + res.status);
    }
    
    const data = await res.json()
    return data.data as Anime
  } catch (error) {
    console.error('Error fetching anime detail:', error)
    return null
  }
}

