// Image Component (Pengganti next/image)
interface CustomImageProps extends React.ImgHTMLAttributes<HTMLImageElement> {
  src: string;
  alt: string;
  width?: number;
  height?: number;
  fill?: boolean;
  priority?: boolean; // Mengindikasikan gambar LCP
}

const CustomImage: React.FC<CustomImageProps> = ({ 
  src, 
  alt, 
  width, 
  height, 
  className = '', 
  fill = false, 
  priority = false, 
  ...props 
}) => {

    // Jika priority=true, gunakan loading="eager" untuk LCP
    const loadingProp = priority ? 'eager' : (props.loading || 'lazy');

    // Untuk mode 'fill' (background hero)
    if (fill) {
        return (
          <img 
            src={src} 
            alt={alt} 
            className={`absolute inset-0 w-full h-full object-cover ${className}`} 
            loading={loadingProp}
            onError={(e) => {
              (e.target as HTMLImageElement).onerror = null; 
              (e.target as HTMLImageElement).src = `https://placehold.co/1920x1080/1f2937/ffffff?text=Poster+Tidak+Tersedia`;
            }}
            {...props} 
          />
        );
    }
    // Untuk mode standar (poster)
    return (
      <img 
        src={src} 
        alt={alt} 
        width={width} 
        height={height} 
        className={className} 
        loading={loadingProp} 
        onError={(e) => {
          (e.target as HTMLImageElement).onerror = null; 
          (e.target as HTMLImageElement).src = `https://placehold.co/${width}x${height}/1f2937/ffffff?text=Image+Missing`;
        }}
        {...props} 
      />
    );
};

