// Button Component
interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: 'default' | 'secondary' | 'outline';
}

const Button: React.FC<ButtonProps> = ({ children, variant = 'default', className = '', ...props }) => {
  let baseClasses = 'px-4 py-2 rounded-lg font-semibold transition-transform duration-200 active:scale-95 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-900 flex items-center justify-center';
  
  switch (variant) {
    case 'secondary':
      baseClasses += ' bg-gray-600 text-white hover:bg-gray-700 focus:ring-gray-500';
      break;
    case 'outline':
      baseClasses += ' bg-transparent border-2 border-indigo-400 text-indigo-400 hover:bg-indigo-400/10 focus:ring-indigo-400';
      break;
    case 'default':
    default:
      baseClasses += ' bg-indigo-500 text-white hover:bg-indigo-600 focus:ring-indigo-500';
      break;
  }
  
  return (
    <button className={`${baseClasses} ${className}`} {...props}>
      {children}
    </button>
  );
};

