// Component untuk tombol interaktif
interface ActionButtonsProps {
  trailerUrl: string | null;
  animeTitle: string;
}

const AnimeActionButtons: React.FC<ActionButtonsProps> = ({ trailerUrl, animeTitle }) => {
  const [isFavorite, setIsFavorite] = useState(false);
  const [message, setMessage] = useState('');

  const showMessage = (text: string) => {
    setMessage(text);
    setTimeout(() => setMessage(''), 3000);
  };

  const handleFavoriteToggle = () => {
    setIsFavorite(!isFavorite);
    showMessage(!isFavorite ? 'Ditambahkan ke Favorit!' : 'Dihapus dari Favorit.');
  };

  const handleWatchTrailer = () => {
    if (trailerUrl) {
      window.open(trailerUrl, '_blank');
    } else {
      showMessage('URL Trailer tidak tersedia.');
    }
  };

  const handleShare = async () => {
    const shareData = {
      title: animeTitle,
      text: `Lihat anime ini: ${animeTitle}!`,
      url: window.location.href,
    };

    try {
      if (navigator.share) {
        await navigator.share(shareData);
      } else {
        const url = window.location.href;
        const el = document.createElement('textarea');
        el.value = url;
        document.body.appendChild(el);
        el.select();
        // Menggunakan document.execCommand('copy') untuk kompatibilitas iframe
        document.execCommand('copy'); 
        document.body.removeChild(el);
        showMessage('Tautan disalin!');
      }
    } catch (err) {
      // Mengabaikan error jika pengguna membatalkan share
      if (err instanceof Error && err.message !== 'Share canceled') {
         console.error('Share gagal:', err);
         showMessage('Gagal berbagi/menyalin tautan.');
      }
    }
  };

  return (
    <>
      <div className="flex flex-wrap gap-3 mt-4 justify-center md:justify-start">
        
        {/* Tombol Watch Trailer */}
        {trailerUrl ? (
          <Button
            className="bg-indigo-500 text-white gap-2 hover:bg-indigo-600 shadow-lg shadow-indigo-500/30"
            onClick={handleWatchTrailer}
          >
            <Play size={18} /> Tonton Trailer
          </Button>
        ) : (
          <Button 
            variant="secondary"
            className="gap-2 bg-gray-600 text-white hover:bg-gray-700 transition-colors"
            onClick={() => showMessage("Demo: Trailer tidak tersedia untuk anime ini.")}
          >
            <Play size={18} /> Trailer
          </Button>
        )}

        {/* Favorites */}
        <Button
          variant="outline"
          className={`gap-2 ${isFavorite 
            ? 'border-red-500 text-red-500 hover:bg-red-500/15' 
            : 'border-indigo-400 text-indigo-400 hover:bg-indigo-400/15'} transition-colors`}
          onClick={handleFavoriteToggle}
        >
          {isFavorite ? <Heart size={18} fill="currentColor" /> : <Heart size={18} />}
          {isFavorite ? 'Sudah Favorit' : 'Tambah ke Favorit'}
        </Button>

        {/* Share */}
        <Button
          variant="outline"
          className="gap-2 border-indigo-400 text-indigo-400 hover:bg-indigo-400/15 transition-colors"
          onClick={handleShare}
        >
          {message.includes('salin') ? <Check size={18} /> : <Share2 size={18} />}
          {message.includes('salin') ? 'Tersalin!' : 'Bagikan'}
        </Button>
      </div>

      {/* Notifikasi Message Box */}
      {message && (
        <div className={`fixed bottom-4 left-1/2 transform -translate-x-1/2 p-3 rounded-xl shadow-2xl text-white font-semibold transition-all duration-300 ease-out z-50 ${message.includes('Gagal') ? 'bg-red-600' : 'bg-green-600'} animate-bounce-in`}>
          {message}
        </div>
      )}
      <style jsx global>{`
        @keyframes bounceIn {
          0% { transform: translate(-50%, 20px) scale(0.9); opacity: 0; }
          60% { transform: translate(-50%, -5px) scale(1.05); opacity: 1; }
          100% { transform: translate(-50%, 0) scale(1); opacity: 1; }
        }
        .animate-bounce-in {
          animation: bounceIn 0.5s ease-out forwards;
        }
      `}</style>
    </>
  );
};

// Komponen Pembantu untuk menampilkan list relasi
type Relation = NonNullable<Anime['relations']>[number];

const RelatedContent = ({ relations }: { relations: Relation[] }) => {
  const validRelations = relations.filter(rel => rel.entry.length > 0);

  if (validRelations.length === 0) return null;

  return (
    <section className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 mb-12">
      <h2 className="text-2xl font-bold text-gray-100 mb-4 border-b border-gray-700 pb-2">Konten Terkait</h2>
      <div className="space-y-4">
        {validRelations.map((rel, index) => (
          <div key={index} className="flex flex-col md:flex-row gap-2 md:gap-4 p-3 bg-gray-800 rounded-xl border border-gray-700 items-start hover:bg-gray-700 transition-colors shadow-md">
            <span className="font-semibold text-indigo-400 min-w-[120px] shrink-0 text-sm md:text-base">
              {rel.relation}:
            </span>
            <div className="flex flex-wrap gap-2">
              {rel.entry.map((entry) => (
                <a 
                  key={entry.mal_id} 
                  href={`/anime/${entry.mal_id}`} 
                  className="px-3 py-1 bg-gray-700 rounded-full text-gray-400 hover:text-gray-100 hover:bg-indigo-500 transition-all text-sm shadow-inner"
                  title={entry.name}
                >
                  {entry.name} ({entry.type})
                </a>
              ))}
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

// Komponen Utama Detail Anime
const AnimeDetailView: React.FC<{ anime: Anime }> = ({ anime }) => {
    // Helper untuk memformat tanggal
    const formatDate = (dateString: string | undefined | null) => {
      if (!dateString) return 'N/A';
      return new Date(dateString).toLocaleDateString('id-ID', { year: 'numeric', month: 'long', day: 'numeric' });
    };

    const startDate = formatDate(anime.aired?.from);
    const endDate = anime.aired?.to ? formatDate(anime.aired.to) : anime.status === 'Finished Airing' ? 'Selesai' : 'Sedang Tayang';

    const renderInfo = (icon: React.ReactNode, title: string, value: string | number | null | undefined) => {
      if (value === undefined || value === null || value === '') return null;
      return (
        <div className="flex items-center space-x-3 p-3 bg-gray-800 rounded-xl shadow-inner hover:bg-gray-700 transition-colors">
          <div className="text-indigo-400 flex-shrink-0">{icon}</div>
          <div>
            <p className="text-xs text-gray-400 font-medium">{title}</p>
            <p className="text-sm font-semibold text-white truncate">{value}</p>
          </div>
        </div>
      );
    };

    return (
        <div className="min-h-screen bg-gray-900 text-white pt-16">
            
            {/* Hero Section / Background Blur */}
            <div className="relative h-96 overflow-hidden">
                <CustomImage 
                    src={anime.images.jpg.large_image_url}
                    alt={`${anime.title} background`}
                    fill={true}
                    className="opacity-20 blur-xl"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-gray-900 to-transparent"></div>
            </div>

            {/* Content Section */}
            <main className="relative -mt-64 pb-12">
                <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
                    
                    <div className="flex flex-col md:flex-row gap-8">
                        
                        {/* Poster */}
                        <div className="flex-shrink-0 w-full md:w-64 -mt-24 shadow-2xl rounded-xl overflow-hidden">
                            <CustomImage
                                src={anime.images.jpg.large_image_url}
                                alt={anime.title}
                                width={500}
                                height={700}
                                priority={true} // Penting untuk LCP/Performa
                                className="w-full h-auto object-cover"
                            />
                        </div>

                        {/* Judul & Aksi */}
                        <div className="md:mt-0 mt-4 flex-grow">
                            <h1 className="text-4xl sm:text-5xl font-extrabold text-white leading-tight mb-2">
                                {anime.title}
                            </h1>
                            {anime.title_english && (
                                <p className="text-xl text-indigo-300 font-light italic mb-4">{anime.title_english}</p>
                            )}
                            
                            {/* Actions & Rating */}
                            <div className="flex flex-col gap-4">
                                
                                {/* Rating Badge */}
                                <div className="flex items-center gap-3">
                                  {anime.score && (
                                    <span className="flex items-center text-lg font-bold text-yellow-400 bg-gray-800 px-4 py-1 rounded-full shadow-lg">
                                        <Star size={18} fill="currentColor" className="mr-2"/>
                                        {anime.score.toFixed(2)}
                                    </span>
                                  )}
                                  <span className={`px-3 py-1 text-sm rounded-full font-semibold ${anime.status === 'Finished Airing' ? 'bg-green-500 text-white' : 'bg-blue-500 text-white'}`}>
                                      {anime.status}
                                  </span>
                                </div>
                                
                                {/* Tombol Aksi */}
                                <AnimeActionButtons 
                                  trailerUrl={anime.trailer?.embed_url || null} 
                                  animeTitle={anime.title}
                                />
                            </div>
                        </div>
                    </div>
                    
                    {/* Ringkasan & Info Detail */}
                    <div className="mt-12 grid grid-cols-1 lg:grid-cols-3 gap-8">
                        
                        {/* Kolom Kiri: Sinopsis */}
                        <div className="lg:col-span-2">
                            <h2 className="text-2xl font-bold text-gray-100 mb-4 border-b border-gray-700 pb-2">Sinopsis</h2>
                            <p className="text-gray-300 leading-relaxed text-base whitespace-pre-wrap">
                                {anime.synopsis || 'Sinopsis tidak tersedia.'}
                            </p>

                            <h3 className="text-xl font-bold text-gray-100 mt-8 mb-4 border-b border-gray-700 pb-2">Genre</h3>
                            <div className="flex flex-wrap gap-2">
                                {anime.genres?.map((genre) => (
                                    <span key={genre.mal_id} className="px-3 py-1 text-sm bg-indigo-500 rounded-full text-white font-medium shadow-md">
                                        {genre.name}
                                    </span>
                                ))}
                            </div>
                        </div>

                        {/* Kolom Kanan: Meta Data */}
                        <div className="lg:col-span-1 space-y-4">
                            <h2 className="text-2xl font-bold text-gray-100 mb-4 border-b border-gray-700 pb-2">Info Detail</h2>
                            
                            {renderInfo(<Film size={20} />, 'Tipe', anime.type)}
                            {renderInfo(<Activity size={20} />, 'Status', anime.status)}
                            {renderInfo(<BookOpen size={20} />, 'Sumber', anime.source)}
                            {renderInfo(<Play size={20} />, 'Episode', anime.episodes)}
                            {renderInfo(<Calendar size={20} />, 'Tahun Tayang', anime.year)}
                            {renderInfo(<Calendar size={20} />, 'Tayang Sejak', startDate)}
                            {renderInfo(<Calendar size={20} />, 'Tayang Hingga', endDate)}
                            {renderInfo(<Star size={20} />, 'Studio', anime.studios?.map(s => s.name).join(', ') || 'N/A')}

                        </div>
                    </div>
                </div>

                {/* Konten Terkait (Relations) */}
                {anime.relations && anime.relations.length > 0 && (
                  <div className="mt-12">
                    <RelatedContent relations={anime.relations} />
                  </div>
                )}
            </main>
        </div>
    );
};


// =============================================================
// 4. KOMPONEN ROUTE UTAMA (Client Component Wrapper)
// =============================================================

interface AnimePageProps {
  // Tipe untuk params dalam Next.js App Router
  params: { id: string }
}

export default function AnimePage({ params }: AnimePageProps) {
  
  // Ambil ID dari params
  const id = params.id;
  const animeId = parseInt(id)
  
  // State: undefined=initial, true=loading, Anime|null=data
  const [anime, setAnime] = useState<Anime | null | undefined>(undefined);
  const [loading, setLoading] = useState(true);

  // Data Fetching menggunakan useEffect
  useEffect(() => {
    // 1. Cek ID rute tidak dapat di-parse menjadi angka
    if (isNaN(animeId) || animeId <= 0) {
        setAnime(null); // Anggap ID tidak valid
        setLoading(false);
        return;
    }

    const fetchData = async () => {
      setLoading(true);
      const data = await getAnimeDetail(animeId);
      setAnime(data);
      setLoading(false);
    };

    fetchData();
  }, [animeId]); // Tergantung pada ID

  // Tampilkan Loader
  if (loading || anime === undefined) {
    return (
      <>
        <Navbar />
        <Loader />
      </>
    );
  }

  // Tampilkan Not Found
  if (anime === null) {
    return (
      <>
        <Navbar />
        <NotFoundPage id={id} />
      </>
    );
  }

  // Tampilkan Detail Anime
  return (
    <>
      <Navbar />
      <AnimeDetailView anime={anime} />
    </>
  );
}